
@media (max-width: 380px) {
.stats {
    margin: auto -20px;
}
.stats > div > span {
    font-size: 0.7em !important;
}
}
.stats > div {
  margin: 20px auto;
}
.stats > div > span {
  font-size: 0.9em;
  padding: 5px;
  border-radius: 5px;
  /*font-weight: bold;*/
  color: white;
}
.stats span.suppr {
  background-color: #ff5555;
}
.stats span.late {
  background-color: #ff9849;
}
.stats span.normal {
  background-color: #27c795;
}
.stats span.total {
  background-color: #06C7EA;
}
.stats span.health {
  background-color: #D02D81;
}
.stats span.number {
  font-weight: bold;
  padding-left: 3px;
}



